#root > div > div {
	text-align: center;
}

#button {
	font-size: 16px;
	font-weight: 200;
	letter-spacing: 1px;
	padding: 13px 20px 13px;
	outline: 0;
	border: 1px solid black;
	cursor: pointer;
	position: relative;
	background-color: rgba(0, 0, 0, 0);
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;

	/* display:grid;
  place-items: center; */
}

#button:after {
	content: '';
	background-color: #6a8e7f;
	width: 100%;
	z-index: -1;
	position: absolute;
	height: 100%;
	top: 7px;
	left: 7px;
	transition: 0.2s;
}

#button:hover:after {
	top: 0px;
	left: 0px;
}

@media (min-width: 768px) {
	#button {
		padding: 13px 50px 13px;
	}
}

#navigate {
	font-family: 'Alata', sans-serif;
}
